








import { UploadImgOrigin } from '@/components/widgets';
import { Component, Vue, Prop } from 'vue-property-decorator';
@Component({
  components: {UploadImgOrigin,},
})
export default class ViewComponent extends Vue {
  /**prop */
  @Prop({
    // type:Object,
    default:()=> {
      return {
        idCardCode: '', // *测试数据
      }
    }
  }) prop!: any;

  /**data */
  propUploadImgOriginFront = {
    api: 'uploadIdImage',
    params: {
      token: this.$store.state.token,
      front: true,
      idCardCode: this.prop.idCardCode,
    }
  }
  propUploadImgOriginBack = {
    api: 'uploadIdImage',
    params: {
      token: this.$store.state.token,
      front: false,
      idCardCode: this.prop.idCardCode,
    }
  }
}
