/**
 * @param {String} url
 * @description 从URL中解析参数
 * @Author: lipeng
 */
export const getUrlParamJson = () => { // 获取参数对象
  const paramObj: any = {}

  if ( location.search === ''){
    return paramObj
  }

  const url = location.href
  const keyValueArr = url.split('?')[1].split('&')
  keyValueArr.forEach(
    (item) => {
      const keyValue = item.split('=')
      paramObj[keyValue[0]] = keyValue[1]
    }
  )
  return paramObj
}
export const getUrlParam = (key: string) => { // 获取url对应参数值
  const urlParamJson = getUrlParamJson();
  const value = urlParamJson[key] || '';
  return value
}


/**
 * @description 倒计时读秒方法
 * @param {*} conf 
 * @Author: lipeng
 */
export const countdownSecond = (conf: any) => {
  const {
    self, // 组件自身
    dataSelector, // data选择器
    textConcat, // 文案拼接方法
    timeout, // 倒计时秒数
    callback, // 回调方法
  } = conf
  let timeoutSecond = timeout
  
  const timer = setInterval(
    () => {
      timeoutSecond--
      self[dataSelector] = textConcat(timeoutSecond)
      if (timeoutSecond === 0) {
        callback()
        clearInterval(timer)
      }
    },
    1000
  )
}

/**
 * *待验证：可以用性
 * @description node创建多级目录
 * @param {*} dirname 
 * @Author: lipeng
 */
const fs = require('fs');
const path = require('path');
export const mkdirsSync = (dirname: string) => {
  if (fs.existsSync(dirname)) {
    return true;
  }else{
      if (mkdirsSync(path.dirname(dirname))) {
          fs.mkdirSync(dirname);
          return true;
      }
  }
}


/**
 * 去抖
 * @param {*} fn // 回调方法
 * @param {*} delay // 延迟执行时间
 */
export function debounce(
  fn: () => void, 
  delay: number = 400
) {
  let timer: null | number = null;

  return () => {
    const args  = arguments; // 回调方法参数
    const context = this; // 自身上下文

    if (timer) { // 每次触发后清除时间函数值
      clearTimeout(timer);
    }
    
    timer = setTimeout(() => {
      fn.apply(context, args as any);
    }, delay);
  }
}


/**
 * 节流
 * @param {*} fn // 回调方法
 * @param {*} delay // 延迟执行时间
 * @param {*} immediate // 是否立刻执行一次
 */
export function throttle(
  fn: () => void,
  delay: number = 400,
  immediate: boolean | null = null /** 是否立刻执行一次 */
){
  // 立即执行
  if (immediate === true) {
    let lastTime: null | number = null // 上次执行时间

    return () => {
      const context = this  // 自身上下文
      const args = arguments // 回调方法参数
      const now = +new Date() // 当前时间

      if (
        lastTime === null  // 第一次执行（上次执行时间为空)
        || now >= lastTime + delay // 或超过限制时间节点（当前时间>=上次执行时间+延迟时间）时
      ) {
        fn.apply(context, args as any)
        lastTime = now // 重置上次执行时间为当前值
      }
    }
  }

  // 非立即执行
  let lastTime: null | number = null // 上次执行时间
  let timer: null | number = null // 时间函数

  return function(){
    const context = this // 自身上下文
    const args = arguments // 回调方法参数
    const now = Date.now() // 当前时间
    
    
    if (lastTime === null) { // 第一次执行(上次执行时间为空)
      if (timer === null) { // 未触发过（未执行过时间函数）
        timer = setTimeout(
          () => {
            fn.apply(context, args as any) // 回调方法
            lastTime = now + delay // // 重置上次执行时间:为当前值（也可直接Date.now(),性能可能不如这样）
          },
          delay
        )
      }
    }
    else if (now >= lastTime + delay) { // 超过限制时间节点（当前时间>=上次执行时间+延迟时间）时
      fn.apply(context, args as any) // 回调方法
      lastTime = now // 重置上次执行时间:为当前值
    }
  }
}

/**
 * 深拷贝（数组&json数据格式）
 * ***简单写法(可扩充自身调用问题兼容+其他引用类型如RegExp、date)
 * @param obj
 */
export function deepCopy(obj: any) {
  const result: any = Array.isArray(obj) ? [] : {}; // 判断拷贝的要进行深拷贝的是数组还是对象，是数组的话进行数组拷贝，对象的话进行对象拷贝
  for (const key in obj) {
    if (obj.hasOwnProperty(key)) {
      if (typeof obj[key] === 'object') {
        result[key] = deepCopy(obj[key]);   // 递归复制
      } else {
        result[key] = obj[key];
      }
    }
  }
  return result;
}

/**
 * 扩展过滤属性（数组&json数据格式）
 * extend 扩展对象可能有多余,也可能缺少模板属性
 * @param obj 
 * @param extend 
 */
export function extendFilter(obj: any, extend: any) {
    const result: any = Array.isArray(obj) ? [] : {}; // 判断拷贝的要进行深拷贝的是数组还是对象，是数组的话进行数组拷贝，对象的话进行对象拷贝
    for (const key in obj) {
        if (obj.hasOwnProperty(key)) { // 忽略到继承属性
            if (typeof obj[key] === 'object') { // array | obj 引用类型
                result[key] = extendFilter(obj[key], extend[key]) // 递归复制
            } else if (extend[key] && obj[key] !== extend[key]){ // 扩展属性存在且不一致
                result[key] = extend[key]
            } else { // 其余
                result[key] = obj[key]
            }
        }
    }
    return result;
}

/**
 * 下载
 */
export function download (url:string,name:string){
  (async()=>{
    let response = await fetch(url)
    // 内容转变成blob地址
    let blob = await response.blob()
    // 创建隐藏的可下载链接
    let objectUrl = window.URL.createObjectURL(blob)
    let a = document.createElement('a')
    //地址
    a.href = objectUrl
    //修改文件名
    a.download = name
    // 触发点击
    document.body.appendChild(a)
    a.click()
    //移除
    setTimeout(() => document.body.removeChild(a), 1000)
  })()
}

/**
 * 获取域名
 */
export function getTld (hostname:string){ // 获取一级域名
  if(!checkip(hostname) && hostname !== 'localhost'){
    const arr = hostname.split('.')
    const datNumbuer = arr.length -1
    if(datNumbuer === 1){
      return hostname
    }else{
      return arr[datNumbuer-1] + '.' + arr[datNumbuer]
    }
  }else{
    return ''
  }
}
export function checkip(hostname:string){ // 检查是否符合ip格式
  const pattern = /^(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9])\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9]|0)\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9]|0)\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[0-9])$/;
  const flag = pattern.test(hostname);
  if (flag) {
    return true
  }else {
    return false
  }
}
export function checkdomain(hostname:string){ // 检查是否是否符合域名格式
  const pattern = /^([\w-]+\.)+((com)|(net)|(org)|(gov\.cn)|(info)|(cc)|(com\.cn)|(net\.cn)|(org\.cn)|(name)|(biz)|(tv)|(cn)|(mobi)|(name)|(sh)|(ac)|   (io)|(tw)|(com\.tw)|(hk)|(com\.hk)|(ws)|(travel)|(us)|(tm)|(la)|(me\.uk)|(org\.uk)|(ltd\.uk)|(plc\.uk)|(in)|(eu)|(it)|(jp))$/;
  const flag = pattern.test(hostname)
  if (flag) {
    return true
  }else {
    return false
  }
}
/**
 * 是否生产或类生产环境
 */
export function isProdEnvUrl (){
  const hostname = location.hostname
  const pathname = location.pathname
  const tld = getTld(hostname)

  if(tld && tld != 'herokuapp.com' && pathname.indexOf('.html')<0){ // 能获取到二级域名 且 非spa格式url
    return true
  }else{
    return false
  }
}