























import { Component, Vue, Prop } from 'vue-property-decorator';
@Component({
  components: {},
})
export default class ViewComponent extends Vue {
  /**prop */
  @Prop() prop!: any;

  /**data */
  idUploadStatus = ''
  lmBankCardNameAndTailNo = ''

  /**life hook */
  created(){
    const data:any = this.prop.shAccountStatusData;
    if(data && data.resCode === '0000'){
      this.lmBankCardNameAndTailNo = data.message.lmBankCardNameAndTailNo
    }
  }

  /**method */
  async resetPassword(){
    const data = await this.$request({
      api: 'resetPassword',
      params: {
        token:this.$store.state.token,
      },
      originReturnUrl:location.origin,
    })
    if(data.resCode === '0000'){
      this.$request({
        formSubmitData: data,
      })
    }
  }
}
