




























import { OpenAccount, UploadIdCard, AccountDetail } from '@/components/views/account/lmAccount'
import { getUrlParamJson, getUrlParam } from '@/utils/common.ts'
import { Component, Vue } from 'vue-property-decorator';
@Component({
  components: {OpenAccount,UploadIdCard,AccountDetail},
})
export default class ViewComponent extends Vue {
  /**data */
  // 初始化嵌入页参数
  selectedLabel = ''; // 开户页，上传身份证页，开户成功页，开户失败页
  // 账户详情页
  propAccountDetail = {
    lmAccount:this.$store.state.user.bankCard,
    realName:this.$store.state.user.bankCard,
    idCode:this.$store.state.user.bankCard,
    shAccountStatusData:{}, 
  }

  // 账户信息
  realName = ''
  idCardNo = ''
  bankCardNo = ''
  mobile = ''

  // 上传身份证页
  propUploadIdCard = {}

  /**life hook */
  created(){
    if(getUrlParam('result') === 'true'){
      this.selectedLabel = '开户成功页'
    }
    else if(getUrlParam('result') === 'false'){
      this.selectedLabel = '开户失败页'
    }
    else if(this.$store.state.user.bankCard){
      this.selectedLabel = '账户详情页'
    }else{
      this.selectedLabel = '开户页'
    }
  }

  /**method */
  async emitOpenAccount(response: any){
    this.realName = response.realName
    this.idCardNo = response.idCardNo
    this.bankCardNo = response.bankCardNo
    this.mobile = response.mobile

    this.propUploadIdCard = response.idCardNo
    this.selectedLabel = '上传身份证页'
  }
  async emitUploadIdCard(response: any){
    const data = await this.$request({
      api:'lmService/personalRegister', // 1.个人用户开户
      params:{
        token:this.$store.state.token,
        lmRegister:{
          realName: this.realName,
          idCardNo: this.idCardNo,
          bankCardNo: this.bankCardNo,
          mobile: this.mobile,
        },
        originReturnUrl:this.$store.state.originNodeServer,
      },
    })
    if(data.resCode === '0000'){
      this.$request({
        formSubmitData:data
      })
    }else{
      this.selectedLabel = '开户失败页'
    }
  }
  async shAccountStatus(){
    const data = await this.$request({
      api:'shAccountStatus', // 2.上行存管账户状态信息
      params:{
        token:this.$store.state.token,
        lmRegister:{
          realName: this.realName,
          idCardNo: this.idCardNo,
          bankCardNo: this.bankCardNo,
          mobile: this.mobile,
        }
      },
    })
    if(data.resCode === '0000'){
      this.propAccountDetail.shAccountStatusData = data
    }
  }
}
